html, body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	background: #fff;
	font-family: 'Helvetica Neue', arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

#app {
	height: 100%;
}

.site-wrapper {
	max-width: 1024px;
    margin: 0 auto;
}

code, pre {
	background-color: rgba(198, 197, 196, 0.15);
	padding: 4px;
}


dt, dd {
	display: inline-block;
}

dt {
	font-weight: bold;
}

dd {
	margin-right: 10%;
}



